.orderInfo {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 316px;
  flex-direction: column;
  margin-left: 42px;

  .bsTotalBox {
    display: flex;
    width: 100%;
    max-width: 316px;
    flex-direction: column;
    background: #fbfafa;
    padding: 20px 20px;
    border-radius: 20px;
  }

  .bsTotalPrice {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    color: #1c1c1c;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
  }

  .bsDcPrice {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    margin-bottom: 15px;
    color: #1c1c1c;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
  }

  .bsDeliveryPrice {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    margin-bottom: 15px;
    color: #1c1c1c;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
  }

  .bsTotal {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    border-top: #f4f4f4;
    padding-top: 22px;
    margin-top: 7px;
    font-weight: 600;
    font-size: 18px;
    line-height: 140%;
    color: #1c1c1c;
  }

  .bsChBtn {
    width: 100%;
    height: 62px;
    min-height: 62px;
    background: #1c1c1c;
    border-radius: 10px;
    color: #fff;
    margin-top: 20px;
    border: none;
    font-size: 20px;
    cursor: pointer;
    &:hover {
      opacity: 0.7;
      background-color: black;
    }
  }

  .couponAlram {
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: gray;
    margin-top: 22px;
    text-align: start;
  }
}
